.slick-slider button:nth-child(1){
    width: 50px;
    height: 50px;
    background-color: #272c33;
    border-radius: 60px;
    -webkit-border-radius: 60px;
    -moz-border-radius: 60px;
    -ms-border-radius: 60px;
    -o-border-radius: 60px;
    left: 50px;
    z-index: 100;
}
.slick-prev:before {
    content: "<";
    color: red;
    font-size: 30px;
  }
  .slick-slider button:nth-last-child(1){
    width: 50px;
    height: 50px;
    background-color: #272c33;
    border-radius: 60px;
    -webkit-border-radius: 60px;
    -moz-border-radius: 60px;
    -ms-border-radius: 60px;
    -o-border-radius: 60px;
    right: 50px;
}

.slick-next:before {
  content: ">";
  color: red;
  font-size: 30px;
}